body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #0f0e16;
}

.App {
  display: flex;
  justify-content: center;
  align-items: center;
}

.content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 2vmin;
  padding-top: 10vmin;
}

.title {
  margin: 0;
  color: white;
  display: flex;
  -webkit-margin-after: 0;
          margin-block-end: 0;
  -webkit-margin-before: 0;
          margin-block-start: 0;
}

.card {
  background: linear-gradient(148deg, rgb(221, 220, 226) 33%, rgb(106, 110, 147) 86%);
  color: white;
  border-radius: 5%;
  padding: 30px;
  width: 30vmin;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}
.card > .location {
  color: #1f2b3f;
}

form {
  font-family: acumin-pro-wide, sans-serif;
  font-weight: 300;
  font-style: normal;
  color: white;
  font-size: larger;
  padding-bottom: 2vmin;
}
form > div {
  color: white;
  display: flex;
  gap: 10px;
}
form > p {
  display: flex;
}

input {
  border: 0.2px solid white;
  padding: 10px 20px 10px 5px;
  background-color: #0f0e16;
  color: white;
  font-family: acumin-pro-wide, sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: large;
}

.submit {
  font-family: acumin-pro-wide, sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: large;
}

.cf {
  padding: 5px 10px;
  border-radius: 10%;
  border: none;
  background: linear-gradient(346deg, rgba(221, 220, 226, 0) 33%, rgb(211, 214, 241) 86%);
  transition: ease-in 0.4s;
  font-weight: 700;
}
.cf:hover {
  transform: scale(1.15);
  background-color: rgb(209, 208, 208);
}

.c-f-buttons {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
}/*# sourceMappingURL=styles.css.map */